import React, { useState, useEffect } from "react";
import { Row, Col, Divider, Card, Timeline, List } from "antd";
import Avatar from "../assets/avatar.jpg";
import {
  HomeOutlined,
  CodeSandboxOutlined,
  MailOutlined,
  FacebookOutlined,
  InstagramOutlined,
  GithubOutlined,
  LinkedinOutlined,
} from "@ant-design/icons";
import * as data from "../database/skills.json";
const { Meta } = Card;
export const Home = () => {
  const [skills, setSkills] = useState(null);
  useEffect(() => {
    getData();
  });
  const getData = () => {
    setSkills(data.default);
  };
  const social = [
    {
      title: "E-Mail",
      value: "mailto:minhquan.k0r1@gmail.com",
      desc: "minhquan.k0r1@gmail.com",
    },
  ];
  return (
    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
      <Col className="gutter-row" md={{ span: 24 }} lg={{ span: 6 }}>
        <div>
          <Card
            hoverable
            style={{ width: 260, margin: "auto" }}
            cover={<img alt="Avatar" src={Avatar} />}
            actions={[
              <a
                href="https://www.facebook.com/Quan.K0r1"
                style={{ fontSize: "25px" }}
                target="blank"
              >
                <FacebookOutlined />
              </a>,
              <a
                href="https://www.instagram.com/quan.k0r1"
                style={{ fontSize: "25px" }}
                target="blank"
              >
                <InstagramOutlined />
              </a>,
              <a
                href="https://github.com/quankori"
                style={{ fontSize: "25px" }}
                target="blank"
              >
                <GithubOutlined />
              </a>,
              <a
                href="https://www.linkedin.com/in/nguyen-quan-a46518165"
                style={{ fontSize: "25px" }}
                target="blank"
              >
                <LinkedinOutlined />
              </a>,
            ]}
          >
            <Meta
              title="Nguyen Minh Quan"
              description="Back-end / Blockchain Developer"
            />
          </Card>
          <div>
            <Divider>About me</Divider>
            <p>
              I have 3 years experience about coding technically Node.js and Go
              and recently I working and research technically blockchain and
              devOPS . Currently, I found myself a detailed person who love to
              deeply research for high tech.
            </p>
          </div>
          <div>
            <Divider>Contact</Divider>
            <p>
              <List
                itemLayout="horizontal"
                dataSource={social}
                renderItem={(item) => (
                  <List.Item>
                    <List.Item.Meta
                      avatar={<MailOutlined />}
                      title={
                        <a href={item.value} target="blank">
                          {item.title}
                        </a>
                      }
                      description={item.desc}
                    />
                  </List.Item>
                )}
              />
            </p>
          </div>
        </div>
      </Col>
      <Col className="gutter-row" md={{ span: 24 }} lg={{ span: 18 }}>
        <div>
          <Divider style={{ paddingBottom: "20px" }}>Timeline</Divider>
          <Timeline mode="right">
            <Timeline.Item
              label="04/2020 - Present"
              dot={<CodeSandboxOutlined style={{ fontSize: "16px" }} />}
            >
              Tego Global
            </Timeline.Item>
            <Timeline.Item
              label="2016 - 2020"
              color="green"
              dot={<HomeOutlined style={{ fontSize: "16px" }} />}
            >
              Ho Chi Minh University of Technology
            </Timeline.Item>
            <Timeline.Item
              label="09/2019 - 02/2020"
              dot={<CodeSandboxOutlined style={{ fontSize: "16px" }} />}
            >
              Vitop Media
            </Timeline.Item>
            <Timeline.Item
              label="06/2018 - 09/2018"
              dot={<CodeSandboxOutlined style={{ fontSize: "16px" }} />}
            >
              GMV Tech
            </Timeline.Item>
            <Timeline.Item
              label="2017 - 2018"
              color="green"
              dot={<HomeOutlined style={{ fontSize: "16px" }} />}
            >
              Khoa Pham Training Center
            </Timeline.Item>
          </Timeline>
          <Divider>Technical</Divider>
          {skills && (
            <List
              grid={{ gutter: 16, column: 4, xs: 1 }}
              dataSource={skills}
              renderItem={(item, index) => (
                <List.Item key={index}>
                  <Card title={item.title}>
                    {item.skills.map((x) => {
                      return <p key={x.title}>{x.title}</p>;
                    })}
                  </Card>
                </List.Item>
              )}
            />
          )}
        </div>
      </Col>
    </Row>
  );
};
