import React, { Fragment, useEffect, useState } from "react";
import { Card, Row, Col, Divider } from "antd";
// import * as data from "../database/projects.json";
const { Meta } = Card;
export const Projects = () => {
  const [projects, setProjects] = useState(null);
  useEffect(() => {
    getData();
  });
  const getData = () => {
    // setProjects(data.default);
    setProjects([]);
  };
  return (
    <Fragment>
      <div>
        <Divider orientation="left">Projects</Divider>
        <Row gutter={{ xs: 8, sm: 16, md: 12, xl: 12 }}>
          {projects &&
            projects.map((x) => {
              return (
                <Col
                  className="gutter-row"
                  sm={{ span: 24 }}
                  md={{ span: 12 }}
                  xxl={{ span: 6 }}
                  key={x.name}
                >
                  <div>
                    <Card
                      style={{
                        width: 300,
                        height: 350,
                        position: "relative",
                        marginBottom: "10px",
                      }}
                      cover={
                        <div style={{ height: "170px" }}>
                          <img
                            style={{
                              position: "relative",
                              top: "50%",
                              width: "100%",
                              padding: "50px",
                              maxHeight: "170px",
                              transform: "translateY(-50%)",
                            }}
                            alt={x.name}
                            src={x.image}
                          />
                        </div>
                      }
                    >
                      <Divider>
                        <div style={{ fontSize: "15px" }}>{x.name}</div>
                      </Divider>
                      <Meta
                        style={{ position: "absolute", bottom: 10 }}
                        title={x.company.name}
                        description={x.date}
                      />
                    </Card>
                  </div>
                </Col>
              );
            })}
        </Row>
      </div>
    </Fragment>
  );
};
