import React, { useState, useEffect } from "react";
import { Layout, Menu, Image } from "antd";
import { LaptopOutlined } from "@ant-design/icons";
import Logo from "../assets/icon.png";
import * as data from "../database/categories.json";
import { Link } from "react-router-dom";
const { SubMenu } = Menu;

export const Sider = () => {
  const [categories, setCategories] = useState(null);
  useEffect(() => {
    getData();
  });
  const getData = () => {
    setCategories(data.default);
  };
  return (
    <Layout.Sider breakpoint="md" collapsedWidth="0" className="layout-sider">
      <div
        className="logo"
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Image width={50} src={Logo} />
      </div>
      <Menu theme="dark" mode="inline">
        {categories &&
          categories.map((x, key) => {
            let folder = x.title.toLowerCase();
            return (
              <SubMenu icon={<LaptopOutlined />} title={x.title} key={key}>
                {x.blogs.map((blog) => {
                  const url = `/blog?folder=${folder}&path=${blog.path}`;
                  return (
                    <Menu.Item key={blog.title}>
                      <Link to={url}>{blog.title}</Link>
                    </Menu.Item>
                  );
                })}
              </SubMenu>
            );
          })}
      </Menu>
    </Layout.Sider>
  );
};
