import { Home } from "./layouts/Home";
import { Projects } from "./layouts/Projects";
import { Blog } from "./layouts/Blog";
const routes = [
  {
    path: "/",
    exact: true,
    main: Home,
  },
  {
    path: "/projects",
    exact: true,
    main: Projects,
  },
  {
    path: "/blog",
    exact: true,
    main: Blog,
  },
];
export default routes;
